

/////////////////////////////////////////////////////////////
// helper classes start with _ to be recongnized in markup //
// these are used for small tweaks when necessary          //
// TRY NOT TO USE THEM MUCH                                //
/////////////////////////////////////////////////////////////

._txt-center {
  text-align: center;
}
._txt-left {
  text-align: left;
}
._txt-right {
  text-align: right;
}
._txt-justify {
  text-align: justify;
}
// text hide
._txt-invisible {
  color: transparent;
  &::selection {
    color: transparent;
    background: transparent;
    text-shadow: none;
  }
  &::-moz-selection {
    color: transparent;
    background: transparent;
    text-shadow: none;
  }
}


// Break string 
._break {
  -ms-word-break: break-all;
  word-break: break-all;
  word-break: break-word;
  -webkit-hyphens: auto;
  -moz-hyphens: auto;
  hyphens: auto;
}


// Ellipse string 
._ellipsis {
  white-space: nowrap;
  overflow: hidden;
  -ms-text-overflow: ellipsis; // Required for IE8 */
  -o-text-overflow: ellipsis; // Required for Opera */
  text-overflow: ellipsis;
}


/* `layout */


._left {
    float: left !important;
}

._right {
    float: right !important;
}

// Hide visually and from screenreaders, but maintain layout */

._invisible {
    visibility: hidden;
}
// Hide from screenreaders and browsers
//
// Credit: HTML5 Boilerplate

._hidden {
  display: none !important;
}
// Only display content to screen readers
//
// See: http://a11yproject.com/posts/how-to-hide-content/

._sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}

// bg replacement
._bgr {
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
}


// iconfont replacement helful when using iconfont
._ifr {
    font: 0/0;
    color: transparent;
    border: 0;
    text-shadow: none;
}

// Use in conjunction with .sr-only to only display content when it's focused.
// Useful for "Skip to main content" links; see http://www.w3.org/TR/2013/NOTE-WCAG20-TECHS-20130905/G1
// Credit: HTML5 Boilerplate

._sr-only-focusable {
  &:active,
  &:focus {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    clip: auto;
  }
}
// Disabled effect class
._disabled {
    pointer-events: none;
    opacity: 0.5;
}


// Prevent callout

._nocallout {
    -webkit-touch-callout: none;
}

._group {
  // @extend .clearfix;
}

._positioningContext {
  position:relative;
}

._no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}


._align-vm {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

._align-hm {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
}

._in-center {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

// clearfix
._clearfix {
    *zoom: 1;
    &:before,
    &:after {
        display: table;
        content: ' ';
    }
    &:after {
        clear: both;
    }
}

// Visually hide an element
._visually-hidden {
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    clip: rect(0, 0, 0, 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
}

._inline-list {
    margin: 0;
    padding: 0;
    list-style: none;
    li {
        display: inline-block;
    }
}

._stack-list {
    margin: 0;
    padding: 0;
    list-style: none;
    li {
        display: block;
    }
}

